<template>
	<div id="mingpian">

		<div class="cardbanner">
			<img :src="msg.storecfg.image"/>
		</div>
		<div class="mp_de">
			<ul class="mp_deon">
				<li>
					<a href="">
						<div class="mp_de_son">
							<p class="mp_dd">商家名称</p>
							<div class="mp_float_right">{{msg.storecfg.name}}</div>
							<div class="clearfix"></div>
						</div>
					</a>
				</li>
				<li class="mp_center_border">
					<div class="center_de_son">
						<p class="mp_dd">商家电话</p>
						<a href="">
							<!--<div class="mp_float_ri"><img src="../../assets/img/center/jt.png" /></div>-->
							<div class="mp_float_dizi">{{msg.storecfg.reservationCall}}</div>
						</a>
						<div class="clearfix"></div>
					</div>
				</li>
				<li>
					<div class="center_de_son">
						<p class="mp_dd">联系人</p>
						<div class="mp_float_right">{{msg.storecfg.linkman}}</div>
						<div class="clearfix"></div>
					</div>
				</li>
				<li class="mp_center_border">
					<div class="center_de_son">
						<p class="mp_dd">经营范围</p>
						<div class="mp_float_right">{{msg.storecfg.scope}}</div>
						<div class="clearfix"></div>
					</div>
				</li>
				<li>
					<div class="center_de_son">
						<p class="mp_dd">商家地址</p>
						<!--<router-link to="/mapitem">
							<div class="mp_float_ri"> <img src="../assets/img/center/jt.png" /></div>
							
						</router-link>-->
						<div class="mp_float_dizi">{{msg.storecfg.address}}</div>
						<div class="clearfix"></div>
					</div>
				</li>
				
				<div  class="erm">
					<img :src="msg.storecfg.qrcode"/>
				</div>

			</ul>
		</div>

	</div>
</template>

<script>
	import carousel from './../components/carousel.vue'
	import axios from 'axios'
	export default {

		name: 'mingpian',

		data() {
			return {
				bannerList: [{
						"img": require('./../assets/img/index/banner2.jpg')
					},
					{
						"img": require('./../assets/img/index/banner2.jpg')
					},
					{
						"img": require('./../assets/img/index/banner2.jpg')
					},
					{
						"img": require('./../assets/img/index/banner2.jpg')
					},
				],
				msg: {
					"storecfg":{
						
					}
					

				},
			}
		},

		methods: {

		},
		components: {

			banner: carousel,

		},
		mounted: function() {

			var _this = this

			_this.axios({
				method: 'get',

				//url: '//api/addresses/1234567890000'
				url: '/api/cfgdatas'

			}).then(function(res) {
				//console.log(res.data)

				_this.msg = res.data.data

				console.log(_this.msg)

			}).catch(function(err) {
				console.log(err)

			})

		}
	}
</script>
<style src='./../assets/css/user/public.css'></style>
<style src='./../assets/css/user/mingpian.css'></style>
<style>
.erm{
	width: 100%;
}

.erm img{
	display: block;
	width: 10rem;
	margin: 1rem auto;
}
.cardbanner{
	display: block;
	width: 100%;
}

.cardbanner img{
	width: 100%;
}
</style>


